import * as React from 'react';
import Radio from '@mui/material/Radio';
import RadioGroup from '@mui/material/RadioGroup';
import FormControlLabel from '@mui/material/FormControlLabel';
import FormControl from '@mui/material/FormControl';
import FormLabel from '@mui/material/FormLabel';

export default function Radio1() {
  return (
    <div style={{ background: "#f5f5f5", padding: "10px", borderRadius: "4px",marginTop:-40 }}>
    <FormControl >
      <RadioGroup
        row
        aria-labelledby="demo-row-radio-buttons-group-label"
        name="row-radio-buttons-group"
      >
        <FormControlLabel value="6 " control={<Radio />} label="6 " />
        <span style={{ marginLeft: '80px' }}></span>
        <FormControlLabel value="6~ 1 " control={<Radio />} label="6~ 1 " />
        <span style={{ marginLeft: '80px' }}></span>
        <FormControlLabel value="1 ~2 " control={<Radio />} label="1 ~2 " />
        <span style={{ marginLeft: '80px' }}></span>
        <FormControlLabel value="2 ~3 " control={<Radio />} label="2 ~3 " />
        <span style={{ marginLeft: '80px' }}></span>
        <FormControlLabel value="3" control={<Radio />} label="3" />
      </RadioGroup>
    </FormControl>
    </div>
  );
}