import * as React from 'react';
import Radio from '@mui/material/Radio';
import RadioGroup from '@mui/material/RadioGroup';
import FormControlLabel from '@mui/material/FormControlLabel';
import FormControl from '@mui/material/FormControl';
import FormLabel from '@mui/material/FormLabel';

export default function Radio3() {
  return (
    <div style={{ background: "#f5f5f5", padding: "10px", borderRadius: "4px",marginTop:-40 }}>
    <FormControl >
      <RadioGroup
        row
        aria-labelledby="demo-row-radio-buttons-group-label"
        name="row-radio-buttons-group"
      >
        <FormControlLabel value="10% " control={<Radio />} label="10% " />
        <span style={{ marginLeft: '80px' }}></span>
        <FormControlLabel value="10% ~20% " control={<Radio />} label="10%  ~ 20% " />
        <span style={{ marginLeft: '80px' }}></span>
        <FormControlLabel value="20% ~30% " control={<Radio />} label="20%  ~ 30% " />
        <span style={{ marginLeft: '80px' }}></span>
        <FormControlLabel value="30% ~40% " control={<Radio />} label="30%  ~ 40% " />
        <span style={{ marginLeft: '80px' }}></span>
        <FormControlLabel value="40% " control={<Radio />} label="40% " />
      </RadioGroup>
    </FormControl>
    </div>
  );
}